<template>
  <div id="setting">
    <common-header title="设置"></common-header>
    <div class="setting__top">
      <Cell title="账户安全" @click="linkTo('Account')"/>
      <!--<Cell title="黑名单"/>-->
      <!--<Cell title="意见反馈" @click="linkTo('FeedBack')"/>-->
    </div>

    <div class="setting__bottom">
      <Cell title="关于我们" @click="linkTo('About')"/>
    </div>
  </div>
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    components: {
      Cell
    }
  }
</script>

<style lang="scss">
  #setting {
    height: 100%;
    background: #f0f0f0;

    .setting__bottom {
      margin-top: 0.267rem;
    }
  }
</style>





